.gl-font-riffic {
  font-family: var(--riffic);
}

.gl-font-calibri {
  font-family: var(--calibri);
}

.gl-title {
  font-weight: bold;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: 0.18em;
  color: var(--third-color);
  margin-bottom: 18px;
  text-transform: uppercase;

  &:last-child {
    margin-bottom: 0;
  }

  @include breakpoint(tablet) {
    font-size: 16px;
    line-height: 18px;
  }

  @include breakpoint(small-mobile) {
    font-size: 10px;
    line-height: 12px;
    margin-bottom: 16px;
  }
}

.gl-subtitle {
  font-weight: bold;
  font-size: 48px;
  line-height: 56px;
  color: var(--secondary-color);
  margin-bottom: 60px;

  &:last-child {
    margin-bottom: 0;
  }

  @include breakpoint(desktop) {
    font-size: 38px;
    line-height: 48px;
    margin-bottom: 50px;
  }

  @include breakpoint(small-mobile) {
    font-size: 32px;
    line-height: 42px;
    margin-bottom: 40px;
  }
}

.gl-main-block {
  background: #fff;
  box-shadow: 0 0 82px rgba(93, 16, 182, 0.1049);
  border-radius: 20px;
}

.gl-nowrap {
  white-space: nowrap;
}

.gl-social-wrapper {
  display: flex;
  align-items: center;
}

.gl-social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;

  .no-touch &:hover,
  .touch &:active {
    .gl-social-image {
      fill: var(--third-color);
    }
  }
}

.gl-social-image {
  fill: #fff;
  transition: 150ms fill;
}
